.database-container {
	$gap: 2rem;
	.languages-container {
		@for $i from 1 to 3 {
			.tools-container:nth-of-type(#{$i}) {
				position: relative;
			}

			.tools-container:nth-of-type(#{$i}):after {
				content: '';
				position: absolute;
				bottom: -$gap / 2;
				left: 0;
				height: 1px;
				width: 100%;
				background: $color-white;
			}
		}

		.tools-container {
			display: grid;
			grid-template-columns: repeat(2, 1fr);
			place-items: center;
			gap: 2rem;
			padding: 0 1rem;

			.tool-logo {
				height: 3rem;
			}

			#sql-server-logo {
				height: 4rem;
			}

			#sql-server,
			#azure {
				grid-column: span 2;
			}
		}
	}
}
